"use client";
import { useState } from "react";
import Link from "next/link";

export default function SignupPage() {
  const [error, setError] = useState<string | undefined>();
  const [success, setSuccess] = useState<string | undefined>();
  const [loading, setLoading] = useState(false);

  async function onSubmit(e: React.FormEvent<HTMLFormElement>) {
    e.preventDefault();
    setError(undefined);
    setSuccess(undefined);
    setLoading(true);
    const fd = new FormData(e.currentTarget);
    const name = String(fd.get("name") || "").trim();
    const email = String(fd.get("email") || "").trim();
    const password = String(fd.get("password") || "");
    try {
      const r = await fetch("/api/auth/signup", {
        method: "POST",
        headers: { "content-type": "application/json" },
        body: JSON.stringify({ name, email, password }),
      });
      if (r.ok) {
        const data = await r.json();
        setSuccess("Account created! Redirecting to dashboard...");
        setTimeout(() => { location.href = data.redirect || "/dashboard"; }, 1500);
        return;
      }
      const j = await r.json().catch(() => ({ message: "Sign up failed" }));
      setError(j.message || "Sign up failed");
    } catch (err) {
      setError("Connection error. Please try again.");
    } finally {
      setLoading(false);
    }
  }

  return (
    <main className="min-h-screen bg-gradient-to-b from-gray-900 via-gray-800 to-gray-900 text-white flex items-center justify-center px-4 py-12">
      <div className="max-w-md w-full">
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold mb-2 bg-gradient-to-r from-blue-400 to-purple-500 bg-clip-text text-transparent">ðŸ¤– EchoPilotAI</h1>
          <p className="text-gray-400">Create your account</p>
        </div>
        <div className="bg-gray-800/50 backdrop-blur-sm border border-gray-700 rounded-2xl p-8 shadow-xl">
          <form onSubmit={onSubmit} className="space-y-6">
            <div>
              <label htmlFor="name" className="block text-sm font-medium text-gray-300 mb-2">Full Name</label>
              <input type="text" id="name" name="name" required className="w-full px-4 py-3 bg-gray-900/50 border border-gray-700 rounded-lg focus:outline-none focus:border-blue-500 transition-colors text-white placeholder-gray-500" placeholder="John Doe" />
            </div>
            <div>
              <label htmlFor="email" className="block text-sm font-medium text-gray-300 mb-2">Email Address</label>
              <input type="email" id="email" name="email" required className="w-full px-4 py-3 bg-gray-900/50 border border-gray-700 rounded-lg focus:outline-none focus:border-blue-500 transition-colors text-white placeholder-gray-500" placeholder="you@example.com" />
            </div>
            <div>
              <label htmlFor="password" className="block text-sm font-medium text-gray-300 mb-2">Password (optional)</label>
              <input type="password" id="password" name="password" minLength={8} className="w-full px-4 py-3 bg-gray-900/50 border border-gray-700 rounded-lg focus:outline-none focus:border-blue-500 transition-colors text-white placeholder-gray-500" placeholder="Leave blank for magic link access" />
              <p className="text-xs text-gray-500 mt-2">Skip the password and we'll send you a magic link to sign in</p>
            </div>
            {error && (<div className="p-4 bg-red-500/10 border border-red-500/30 rounded-lg"><p className="text-red-400 text-sm">{error}</p></div>)}
            {success && (<div className="p-4 bg-green-500/10 border border-green-500/30 rounded-lg"><p className="text-green-400 text-sm">{success}</p></div>)}
            <button type="submit" disabled={loading} className="w-full bg-gradient-to-r from-blue-500 to-purple-500 hover:from-blue-600 hover:to-purple-600 text-white px-6 py-3 rounded-lg font-semibold transition-all duration-200 transform hover:scale-105 disabled:opacity-50 disabled:cursor-not-allowed disabled:transform-none">{loading ? "Creating account..." : "Create Account"}</button>
          </form>
        </div>
        <div className="mt-6 text-center text-sm text-gray-400">
          <p>Already have an account? <Link href="/login" className="text-blue-400 hover:text-blue-300 transition-colors">Sign In</Link></p>
          <p className="mt-2"><Link href="/pricing" className="text-gray-500 hover:text-gray-400 transition-colors">View Pricing</Link></p>
        </div>
      </div>
    </main>
  );
}
